<template>
  <div v-if="doc.length">
      <v-simple-table>
        <template v-slot:default>
          <thead>
            <tr>
              <th>Method</th>
              <th>Example</th>
              <!-- <th>Arguments</th> -->
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, indx) in doc" :key="indx">
              <td><b>{{item.method}}</b></td>
              <td>{{item.func}}</td>
              <!-- <td>
                <ul>
                  <li v-for="arg in item.args" :key="arg">
                    {{ arg }}
                  </li>
                </ul>
              </td> -->
            </tr>
          </tbody>
        </template>
      </v-simple-table>
      <!-- <pre v-highlightjs>
        <code class="javascript">
        <template v-for="(item) in doc">
          /*** {{ item.method }}
          {{ item.args.join('\n\t') }}

          {{ item.func }}
          */
        </template>
        </code>
      </pre> -->
  </div>
</template>
<script>
export default {
  props: {
    model: {
      type: String,
      required: true
    }
  },
  watch: {
    model (value) {
      this.doc = []
      setTimeout(() => this.load(value), 0)
    }
  },
  data () {
    return {
      doc: [],
      tab: null,
    }
  },
  methods: {
    load (model) {
      const args = {
        id: '[String]',
        body: '[Object] ',
        query: '[Object]',
        params: '[Object]',
        callback: '[Function]: return (error, data)',
      }
      const STRIP_COMMENTS = /((\/\/.*$)|(\/\*[\s\S]*?\*\/))/mg
      const ARGUMENT_NAMES = /([^\s,]+)/g
      const api = this?.$sdk?.api[model]
      const methodsDefault = Object.getOwnPropertyNames(api.__proto__.__proto__)
      const methodsOwner = Object.getOwnPropertyNames(api.__proto__)
      const methods = JSON.parse(JSON.stringify(methodsOwner.concat(methodsDefault)))
      const deletes = ['constructor', 'req', 'get', 'post', 'put', 'delete']
      function getParamNames(func) {
        var fnStr = func.toString().replace(STRIP_COMMENTS, '');
        var result = fnStr.slice(fnStr.indexOf('(')+1, fnStr.indexOf(')')).match(ARGUMENT_NAMES);
        if(result === null)
           result = [];
        return result;
      }
      for (let i = 0; i < methods.length; i++) {
        if (deletes.includes(methods[i])) delete methods[i]
      }
      methods.sort().map(method => {
        const _arguments = getParamNames(api[method])
        const _argumentsParse = _arguments.reduce((acc, arg) => {
          acc.push('\t'+ arg + ' ' + args[arg])
          return [ ...acc ]
        }, [])
        const _function = `\tnodriza.api.${model}.${method}(${_arguments.join(', ')})`
        return {
          method: this.$capitalize(method),
          args: _argumentsParse || null, 
          func: _function || null
        }
      }).map(doc => this.doc.push(doc))
    }
  },
  mounted () {
    this.load(this.model)
  }
}
</script>

<style>
  ul {
    list-style: none;
    padding-left: 0 !important;
  }
</style>


