import Store from '@/store/index.js'
import Guide from '@/views/Guide.vue'
import Reference from '@/views/Reference.vue'
import ReferenceModel from '@/components/reference/ReferenceModel'

export const getGuideRoute = () => {
  const name = 'guide'
  const sidebar = [
    {
      heading: 'guide',
    },
    {
      text: 'Get Started',
      routeName: 'get-started' 
    },
    {
      text: 'Reference',
      routeName: 'reference' 
    },
  ]
  Store.commit('setSidebar', { name, sidebar })
  return [
    {
      path: '/' + name,
      name: name,
      component: Guide,
    },
    {
      path: '/get-started',
      name: 'get-started',
      redirect: { name }
    }
  ] 
}

export const getReferenceRoute = () => {
  const sidebar = []
  const name = 'reference'
  const routes = [{
    path: '/' + name,
    name: name,
    component: Reference,
  }]
  const capitalize = str => str.charAt(0).toUpperCase() + str.slice(1)
  const getPath = (basepath, path) => ('/' + basepath + '-' + path)
  const getName = (basepath, path) => (basepath + '-' + path)
  const createRoute = (basepath, model) => ({
    path: getPath(name, model),
    name: getName(name, model),
    meta: { model, title: capitalize(model) },
    component: ReferenceModel
  })
  const createSidebarItem = (model, route, isHeading = false) => ({
    routeName: route.name,
    text: capitalize(model),
    heading: isHeading ? model.charAt(0).toUpperCase() + model.slice(1) : false
  })
  Store?.state?.models?.map((model, index) => {
    const route = createRoute(name, model)
    if (!index) sidebar.push(createSidebarItem(name, { name }, true))
    sidebar.push(createSidebarItem(model, route))
    routes.push(route)
  })
  Store.commit('setSidebar', { name, sidebar })
  return routes
}