import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentView: 'guide',
    sidebar: {},
    models: [
      'proposal',
      'webhook',
      'user', 
      'lead'
    ].sort(),
  },
  getters: {
    getSidebar (state) {
      return state.sidebar[state.currentView]
    }
  },
  mutations: {
    setSidebar (state, { name, sidebar}) {
      state.sidebar[name] = sidebar
    },
    setCurrentView (state, currentView) {
      state.currentView = currentView
    }
  },
  actions: {
    
  },
  modules: {
    
  }
})
