<template>
  <v-container fluid>
    <v-row>
      <v-col>
        <h1>{{ $route.meta.title }}</h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Laboriosam a unde corrupti consequatur tempore doloribus at error eveniet omnis quia exercitationem amet placeat, cum sit harum voluptatibus quibusdam nisi perferendis!
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <b>NOTE:</b> callback function returns two params (error, data)
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-tabs v-model="tab">
          <v-tab>Schema</v-tab>
          <v-tab>Methods</v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item><Schema v-if="isShow" :model="model" /></v-tab-item>
          <v-tab-item><Methods v-if="isShow" :model="model" /></v-tab-item>
        </v-tabs-items>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  computed: {
    model () {
      return this.$route?.meta?.model
    }
  },
  created () {
    this.$emitter.$on('changeRoute', () => {
      this.isShow = false
      setTimeout(() => {
        this.isShow = true
      }, 1)
    })
  },
  data () {
    return {
      tab: null,
      isShow: true,
    }
  },
}
</script>